.Promotion {
  padding-top: 2rem;
  margin-bottom: 5rem;
}
.ProductOffers {
  background-color: #f0f7fa;
  padding: 4rem 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
@keyframes mymove {
  0% {
    transform: rotateY(0deg);
  }
  15% {
    transform: rotateY(90deg);
  }
  30% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(0deg);
  }
}
.VariantTitle {
  display: inline-block;
  text-align: center;
  color: #234976;
  margin: auto;
  padding: 0;
  margin-bottom: 1rem;
  position: relative;
}
.VariantTitle::after {
  content: "";
  width: 90%;
  position: absolute;
  height: 0.2rem;
  background-color: #234976;
  border-radius: 10rem;
  overflow: hidden;
  bottom: -0.3rem;
  left: 5%;
  transition: all 0.5s ease-in-out;
  animation: mymove 3s infinite;
}


.Variants {
  font-weight: 600;
  color: gray;
  margin: 0.5rem 0;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.Variants > div {
  background-color: var(--clr-wb-100);
  border: 2px solid var(--clr-wb-200);
  border-radius: 1.6rem;
  padding: 0.5rem 0.7rem;
  margin: 0.2rem;
}
.Variants > div:hover {
  background-color: var(--background-gray);
}
.EveryWater {
  color: #234976;
  font-size: 1.3rem;
  text-align: center;
  margin: 0;
  margin-bottom: 2rem;
  position: relative;
}
.EveryWater::after {
  content: "";
  width: 10%;
  position: absolute;
  height: 0.3rem;
  background-color: #234976;
  border-radius: 10rem;
  overflow: hidden;
  bottom: -0.3rem;
  left: 45%;
}
.Offer {
  background-color: white;
  width: 90%;
  margin: auto;
  padding: 1.5rem 1rem;
  border-radius: 1.5rem;
  box-shadow: 0 0 1rem 0.3rem rgb(0 0 0 / 16%);
}
.AddedValuesContainer {
}
.AddedValuesRow {
  display: flex;
  justify-content: space-evenly;
}
.AddedValues {
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
  max-width: 8rem;
}
.AddedValuesRow:first-child {
  /* margin-top:0.3rem; */
  margin-bottom: 3rem;
}

.AddedValues > p {
  margin: 0;
}
.AddedValues > img {
  width: 70%;
}
.RequestContainer {
  background-color: #e3eaf2;
  padding: 3rem 0rem;
}
.Request {
  width: min(90%, 90rem);
  margin: auto;
}
.RequestTitle {
  margin: 0;
  padding: 0;
  font-size: 1.8rem;
}
.RequestTandC {
  font-size: 0.8rem;
  margin: 0;
  padding: 0;
}
.Reviews {
  background-color: #f4f4f4;
  padding: 2rem 1rem;
}

.Location {
  font-size: 1rem !important;
  margin: 0;
}
.Contact {
  width: 100%;
  text-align: center;
  background-color: #f4f4f4;
  padding-top: 3rem;
}
.ContactTitle {
  text-align: center;
  font-size: 1.4rem;
  margin: 0;
  padding: 1rem 0;
}
.Email {
  text-align: center;
  font-size: 1.4rem;
  color: gray;
  text-decoration: none;
  padding: 1rem 0;
}
.WhatsappButton {
  position: absolute;
  width: 40%;
  height: 60%;
  background-color: transparent;
  top: 50%;
  transform: translateY(-50%);
  left: 13%;
  z-index: 2;
  color: transparent;
}
.TandC {
  color: rgb(179, 175, 175);
  border-top: 0.2px solid rgb(201, 201, 201);
  font-size: 1rem;
  padding: 2rem 0;
  padding-bottom: 4rem;
  width: 50%;
  margin: 0;
  margin: auto;
}
.RequestNumberInput {
  margin: 1.2rem 0px;
  padding: 0.3rem 1rem;
  background-color: transparent;
  width: 170px;
}
.RequestNumberInput:focus {
  outline: none;
}
.RequestSubmit {
  border: 0;
  margin: 1.2rem;
  margin-left: 2rem;
  background-color: #234976;
  color: #fff;
  font-size: 13px;
  font-family: amiko-bold;
  padding: 0.5rem 3rem 0.3rem;
  border-radius: 3rem;
}
.ErrorMessage {
  font-size: 1.2rem;
  font-family: amiko-bold;
  color: red;
  position: absolute;
  top: -2px;
}

@media screen and (min-width: 50em) {
  .TopContent {
    display: flex;
    align-items: center;
    background-color: #f0f7fa;
    justify-content: space-evenly;
  }
  .TopContent > section {
    width: 40%;
  }
  .BottomContent {
    display: flex;
    align-items: center;
    background-color: #f4f4f4;
    justify-content: space-evenly;
    padding-bottom: 7rem;
  }
  .TopImage {
    box-shadow: 0 0 1rem 0.3rem rgb(0 0 0 / 28%);
    width: 95%;
    margin: auto;
  }
  .BottomContent > section {
    width: 40%;
  }
  .whatsappImg {
    box-shadow: 0 0 1rem 0.3rem rgb(0 0 0 / 28%);
  }
  .RequestTitle {
    margin: 0;
    padding: 0;
    font-size: 2.4rem;
  }
  .RequestTandC {
    font-weight: 600;
    font-size: 1.2rem;
    margin: 0;
    padding: 0;
  }

  .RequestNumberInput {
    margin: 2rem 0px;
    padding: 0.6rem 1.5rem;
    background-color: transparent;
    font-size: 1.6rem;
    width: 200px;
  }
  .RequestSubmit {
    border: 0;
    margin: 2rem;
    margin-left: 4rem;
    background-color: #234976;
    color: #fff;
    font-size: 1.6rem;
    font-family: amiko-bold;
    padding: 0.8rem 5rem 0.5rem;
    border-radius: 3rem;
  }
  .ErrorMessage {
    font-size: 1.2rem;
    font-family: amiko-bold;
    color: red;
    position: absolute;
    top: -2px;
  }
}
